const columns = (fn) => {
  return [
    {
      title: '申请时间',
      dataIndex: 'entryTime',
      key: 'entryTime'
    },
    {
      title: '申请类型',
      dataIndex: 'department',
      key: 'department'
    },
    {
      title: '申请人',
      dataIndex: 'staffName',
      key: 'staffName'
    },
    {
      title: '申请内容',
      dataIndex: 'education',
      key: 'education'
    },
    {
      title: '操作',
      dataIndex: 'doed',
      key: 'doed',
      render: (text, record) => (
        <span>
          <Button type="primary" size="small" onClick={()=>fn(record.id,false)}>修改</Button>
          <Divider type="vertical" />
          <Button type="danger" size="small" onClick={() => { fn(record.id,true) }}>删除</Button>
        </span>
      )
    }
  ];
}
