import React, { Component } from 'react'
import { Button,Table,message,Modal} from 'antd';
import Nav from "@/components/topNav/";
import dataed from "@/config/tableList/"
import "./style/style.scss";
import Dialog from "@/components/dialog";
import {removeData} from "@/api/"
import { observer, inject } from 'mobx-react';
const { confirm } = Modal
let edited = []
const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
    if(selectedRowKeys){
      edited = selectedRowKeys
    }
  },
  onSelect: (record, selected, selectedRows) => {
    console.log(record, selected, selectedRows);
  },
  onSelectAll: (selected, selectedRows, changeRows) => {
    console.log(selected, selectedRows, changeRows);
  }
};
@inject('AllList')
@observer
class List extends Component {
    state={
        arr:[
           "全部客户", "合作客户" , "待拓展客户" ,"停止合作客户"],
        ind:0,
        open:false,
        item:{},
        brr:[
          {
            title:"新增",
            background:"#7EB91F",
            borderColor:"#7EB91F"
          },{
            title:"修改",
            background:"#55AAE0",
            borderColor:"##55AAE0"
          },{
            title:"删除",
            background:"#FF9803",
            borderColor:"#FF9803"
          },{
            title:"分配",
            background:"#DD6360",
            borderColor:"#DD6360"
          },{
            title:"设置",
            background:"#64C775",
            borderColor:"#64C775"
          }
        ]
    }
    render() {
        let {ind,arr,open,brr} = this.state;
        let {AllList} = this.props;
        let {allList} = AllList;
        allList = allList.map(item=>{return item}); //将proxy转换成数组
        return (
            <div className="wrap-list">
                <div className="list-top">
                  {
                    brr.map((item,index)=>
                    <Button key={index} type="primary" style={{
                      background:item.background,borderColor:item.borderColor
                    }} onClick={()=>this.btns(item)}
                    >{item.title}
                    </Button>)
                  }
                </div>
                  <Nav navArr={arr} switchFn={this.switchFn}/>
                  {(ind === 0 || ind === 2) ?  <div className="list-bottom">
                  <Table dataSource={allList} columns={dataed.customerList} rowSelection={rowSelection} pagination={false} onRow={this.onClickRow}/>
                </div> : <div className="font">暂无数据</div>
                  }

                  <Dialog  open={open} item={this.state.item} onDialog={this.dialog.bind(this)} edited={edited}/>
            </div>
        )
    }
    switchFn=(index)=>{
      this.setState({
        ind:index
      })
    }
    dialog(open){
      this.setState({
        open:!open
      })
    }
    btns = (item)=>{
      //修改
      if(item.title === "修改"){
        let {AllList} = this.props;
        let {allList} = AllList;
        if(edited.length!== 1){
          message.info('一次编辑一个')
          return;
        }
      this.setState({
        open:true
      })
        let item = allList.find(item=>item.id === edited[0]);
        this.setState({
         item:item
       })
      }
      //新增
      if(item.title === "新增"){
        this.setState({
          open:true,
          item:{}
        })
      }
      if(item.title === "删除"){
        if(!edited.length){
          message.info("请选择删除的项")
        }else{
          let {allList} = this.props.AllList
          confirm({
            title: "确定删除吗？",
            onOk() {
                return new Promise((resolve) => {
                  removeData({id:JSON.stringify(edited)})
                    resolve()
                }).then(() => {     
                  edited.forEach(item => {
                        let ind = allList.findIndex((ele) => ele.id === item)
                        let indArr = []
                        indArr.push(ind)
                        allList.splice(ind, indArr.length)
                    })
                    message.success("删除成功")
                    // console.log(`删除成功`)

                })
            }
        });
        }
         
        
      }
    }
}
export default List