import React, { Component } from 'react'
import { Form, Input, Button, message } from 'antd'
import "./style/style.scss";
import { observer, inject } from 'mobx-react';
import { addList } from "@/api/index";
@inject('AllList')
@inject('ContorlData')
@observer
class Dialog extends Component {
    state = {
        nameValue: "",
        addressValue: "",
        timeValue: "",
        departmentValue: "",
        positionValue: "",
        telValue: "",
        brr: []
    }
    render() {
        let { open, onDialog } = this.props;
        let { nameValue, addressValue, timeValue, departmentValue, positionValue, telValue } = this.state;
        return (
            <div className="dialog">
                {open &&
                    <div className="mask" onClick={() => {
                        onDialog(open)
                    }}
                    >
                    </div>}
                {open && <div className="content">
                    <Form>
                        <Form-Item>
                            <span>客户名称:</span>
                            <Input value={nameValue} onChange={(e) => {
                                this.setState({
                                    nameValue: e.target.value
                                })
                            }}
                            >
                            </Input>
                        </Form-Item>
                        <Form-Item>
                            <span>详细地址:</span>
                            <Input value={addressValue} onChange={(e) => {
                                this.setState({
                                    addressValue: e.target.value
                                })
                            }}
                            >
                            </Input>
                        </Form-Item>
                        <Form-Item>
                            <span>创建时间:</span>
                            <Input value={timeValue} onChange={(e) => {
                                this.setState({
                                    timeValue: e.target.value
                                })
                            }}
                            >
                            </Input>
                        </Form-Item>
                        <Form-Item>
                            <span className="department">职位:</span>
                            <Input value={departmentValue} onChange={(e) => {
                                this.setState({
                                    departmentValue: e.target.value
                                })
                            }}
                            >
                            </Input>
                        </Form-Item>
                        <Form-Item>
                            <span className="position">位置:</span>
                            <Input value={positionValue} onChange={(e) => {
                                this.setState({
                                    positionValue: e.target.value
                                })
                            }}
                            >
                            </Input>
                        </Form-Item>
                        <Form-Item>
                            <span className="position">电话:</span>
                            <Input value={telValue} onChange={(e) => {
                                this.setState({
                                    telValue: e.target.value
                                })
                            }}
                            >
                            </Input>
                        </Form-Item>
                    </Form>
                    <div className="button">
                        <Button onClick={this.detail.bind(this)}>确定</Button>
                        <Button onClick={this.clear}>取消</Button>
                    </div>
                </div>}
            </div>
        )
    }
    componentDidMount(){
        this.props.AllList.all()
    }
    componentWillReceiveProps(nextProps) {
        let { item } = nextProps;
        if (item) {
            this.setState({
                nameValue: item.staffName,
                addressValue: item.address,
                timeValue: item.entryTime,
                departmentValue: item.department,
                positionValue: item.position,
                telValue: item.tel
            })
        } else {
            this.setState({
                nameValue: "",
                addressValue: "",
                timeValue: "",
                departmentValue: "",
                positionValue: "",
                telValue: ""
            })
        }
    }
    detail() {
        let { edited, onDialog, AllList } = this.props;
        let { allList } = AllList;
        let { nameValue, addressValue, timeValue, departmentValue, positionValue, telValue } = this.state;
        let index = allList.findIndex(item => item.id === edited[0]);
        if (allList[index]) {
            allList[index].staffName = nameValue;
            allList[index].address = addressValue;
            allList[index].entryTime = timeValue;
            allList[index].department = departmentValue
            allList[index].position = positionValue
            allList[index].tel = telValue
            message.success('修改成功')
            onDialog(open)
        } else {
            if(nameValue && addressValue && timeValue && departmentValue && positionValue && telValue){
                addList({
                    staffName: nameValue, //姓名
                    address: addressValue, //地址
                    department: departmentValue, //职位
                    position: positionValue,  //位置
                    tel: telValue //电话
                }).then(data => {
                    if (data.code) {
                        message.success("添加成功");
                        onDialog(open)
                        this.props.AllList.all()
                        this.props.ContorlData.getStaffListFn()
                    }
                })
            }else{
                message.success("输入框不能为空");
            }
        }
    }
    clear = () => {
        let { onDialog } = this.props;
        this.setState({
            nameValue: "",
            addressValue: "",
            timeValue: "",
            departmentValue: "",
            positionValue: "",
            telVal: ""
        })
        onDialog(open)
    }
}
export default Dialog