import React, { Component } from 'react'

class TopNav extends Component {
    state = {
        ind: 0
    }
    render() {
        let { navArr, switchFn } = this.props
        let { ind } = this.state
        return (
            <div className="top-nav">
                {
                    navArr.map((item, index) =>
                    <span key={index} className={ind === index ? "active" : ""} onClick={() => {
                        this.setState({
                            ind: index
                        })
                        switchFn(index)
                    }}
                    >{item}</span>)
                }
            </div>
        )
    }
}

export default TopNav;
